.contact{
    padding-top: 99px;
    width: 100%;
    background-color:$negro;
    overflow: hidden;
    height: 100vh;
    position: relative;

    &::before {
            content: 'Conect';
            position: absolute;
            bottom: -20px;
            right: 0px;
            font-weight: 900;
            font-size: 9rem;
            color: lighten($color: $negro, $amount: 3)
        }

    .contenedor-contacto{
        position: relative;
        margin: 0 auto;
        max-width: 80%;
        height: 400px;

        

        h2 {
            font-size: 4rem;
            color: $amarillo;
        }
        .logo-contacto{
            img{
                width: 160px;
            }
        }
    }

    p{  
        text-align: center;
        color: $blanco;
        font-size: 2rem;
    }

    .datos-contacto{
        color: $amarillo;
        h4{
            padding: 1rem;
            display: inline-block;
            font-weight: 300;
            font-size: 2rem;
            background-color:$amarillo;
            color: $negro;
            border-radius: 5px;
            margin: .5rem;
        }

    }

   

}
