.contenedor-Perfil{
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3rem;
    padding: 3rem;
    @include d-grid(3, 0rem);
    justify-content: center;
    background-color: $negro;
    border-radius: 1rem;

    hr{
        color: $amarillo;
        opacity: .3;
        width: 100%;
        float: left;
    }
    // izquierda
    .zona-Izquierda{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        padding: 0 0rem;
        
        h2{
            // padding: 0rem 2rem;
            font-weight: 600;
            font-size: 4rem;
            line-height: 3rem;
            text-align: end;
            color: $amarillo;

        }
        .descripcion{
            p{
                font-size: 1.2rem;
                color: $blanco;
                text-align: right;
            }
        }
        .redes-perfil{
            // width: 100%;
            background-color: $amarillo;
            color: white;
            border-radius: .3rem;
            overflow: hidden;
            display: flex;
            position: relative;

            

            .titulo{
                background-color: $blanco;
                height: 100%;
                color: $negro;
                font-size: 1.2rem;
                padding: .5rem 1rem;
            }
            .contenedor-iconos-redes{
                padding: 0 1rem;
                color: $negro;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .iconos{
                    cursor: pointer;
                    margin: 0 .5rem;
                    font-size: 1rem;
                    transition: .2s ease-out;

                    &:hover{
                        color: darken($color: $amarillo, $amount: 20);
                        transform: scale(1.1);
                        margin: 0 1rem;
                    }
                }
            }
        }
    }
    // centro
    .contenedor-foto-perfil{
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .2s ease;
        position: relative;
        cursor: pointer;
        &:hover{
            transform: scale(1.1);

            .edad{
                transform: scale(1.2);
            }
            .foto-perfil{
                border: lighten($color: $amarillo, $amount: 0) 15px solid;
            }
        }
        .edad{
            position: absolute;
            bottom: 0;
            left: 50%;
            z-index: 10;
            width: 4rem;
            height: 4rem;
            font-size: 2rem;
            background-color: $amarillo;
            color: $negro;
            font-weight: 300;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .2s ease-out;
            transition-delay: .1s;
        }
        .foto-perfil{
            width: 15rem;
            height: 15rem;
            border-radius: 50%;
            overflow: hidden;
            background-color: $blanco;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .3s ease-out;
            transition-delay: .1s;
            border: lighten($color: $amarillo, $amount: 40) 5px solid;
        }
    }
    // derecha
    .zona-derecha{
        .secciones{
            .titulo{
                font-size: 1.3rem;
                color: $amarillo;
            }
            .iconos{
                color: lighten($color: $negro, $amount: 80);
                font-size: 1rem;
                margin: .5rem 0;
            }
        }
    }
}