.clientes_experiencia{
    width: 80%;
    margin: 4rem auto 0rem auto;;
    text-align: center;
    h4{
        font-size: 4rem;
    }
    p{
        font-size: 2rem;
    }
    .contenedor-titulo{
        // display: flex;
        text-align: center;
        padding: 2rem;

    }
    .clientes-titulo{
        @include flex-center;
        margin: .4rem;
        display: inline-block;
        padding: .5rem 1rem;
        color: $negro;
        border-radius: .5rem;
        background-color: $blanco   ;
        font-weight: 600;
        font-size: 1.2rem;
        cursor: pointer;
        transition: .2s ease-out;


        &:hover{
            background-color: $negro;
            color: $blanco;
            transform: scale(1) translateY(-2px);
            margin: .2rem .6rem;
        }
    }
}